* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.card > .img-container > img {
    width: 100%
  }

  .card {
        
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 250px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    
  }
.jumbotron, .titleHead {
    width: 100%;
    text-align: center;
    
    font-family: 'Special Elite', cursive;
    background: black;
    color: white;
}
